@charset "utf-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul {
  list-style: none;
}
@font-face {
  font-family: main1;
  src: url(./static/font/Jalnan.woff) format(woff);
}

.App {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
}
.MainWrap {
  width: 500px;
  position: relative;
  background-color: #ff5c00;
}
.MainWrap > div {
  width: 90%;
  padding: 10px 0;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
}
.title {
  font-family: main1;

  margin: 20px;
  text-align: center;
}
.BtnWrap {
  display: flex;
  justify-content: center;
}

.BoxWrap {
  margin: 10px;
}
.BoxWrap > li {
  height: 10px;
  line-height: 15px;
  display: flex;
  justify-content: center;
}
#CheckBox {
  display: block;
  width: 15px;
  height: 15px;

  border-radius: 2px;
  margin: 0 10px;
}
#CheckBox:hover {
  border: 2px solid #ff5c00;
  background-color: #ff5c00;
  cursor: pointer;
}
.CheckBoxColor {
  border: 2px solid #ff5c00;
  background-color: #ff5c00;
}

.CheckBoxColor2 {
  border: 2px solid #ccc;
  background-color: #fff;
}
#Btn {
  display: block;
  padding: 10px;
  width: 80px;
  height: 30px;

  border-radius: 20px;
  outline: none;
  border: none;
  font-family: main1;
  color: #fff;

  text-align: center;
  margin: 10px;
}
.BtnClicked {
  background: #ff5c00;
}
.BtnClickednone {
  background: #ccc;
}
.addinput {
  background-color: #eee;
  width: 300px;
  height: 80px;
  line-height: 75px;
  border-radius: 20px;
  margin: 0 auto;
}
.addinput input {
  border: none;
}
.RandomResult {
  width: 100%;
  /* border: 1px solid #ff5c00; */
  position: relative;
  text-align: center;
  font-family: main1;
}
.RandomResultWrap {
  width: 100%;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.RandomResultWrap > div {
  font-size: 30px;
}
.lastmenu {
  color: #ff5c00;
  font-size: 40px;
}
.RandomResultWrap > p {
  padding-top: 20px;
  color: #ccc;
}

/* respon */
@media (max-width: 1200px) {
  .title {
    font-size: 25px;
  }
  .addBoxWrap {
    display: none;
  }
}
@media (max-width: 800px) {
  .App {
    flex-direction: column;
  }
  .MainWrap {
    width: 100%;
    height: 40%;
    position: relative;
    background-color: #ff5c00;
  }

  .MainWrap > div {
    width: 90%;
    padding: 10px 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background-color: #fff;
    border-radius: 10px;
  }
  .RandomResult {
    height: 100%;
  }
  .BtnWrap > #Btn:last-child {
    display: none;
  }
}
@media (max-width: 500px) {
  .App {
    flex-direction: column;
  }
  .MainWrap {
    width: 100%;
    height: 50%;
    position: relative;
    background-color: #ff5c00;
  }

  .MainWrap > div {
    width: 90%;
    padding: 10px 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background-color: #fff;
    border-radius: 10px;
  }
  .RandomResult {
    height: 100%;
  }
  .RandomResultWrap > div {
    font-size: 20px;
  }
  .lastmenu {
    color: #ff5c00;
    font-size: 30px;
  }
  .RandomResultWrap > p {
    padding-top: 20px;
    color: #ccc;
    font-size: 14px;
  }
  .BoxWrap > li {
    height: 10px;
    line-height: 10px;
    display: flex;
    align-items: baseline;
  }
  .BoxWrap > li > div {
    font-size: 14px;
  }
  #CheckBox {
    width: 10px;
    height: 10px;
  }
}
