@charset "utf-8";

#Btn {
  display: block;
  padding: 10px;
  width: 80px;
  height: 30px;

  border-radius: 20px;
  outline: none;
  border: none;
  font-family: main1;
  color: #fff;

  text-align: center;
  margin: 10px;
}

.BtnClicked {
  background: #ff5c00;
}
.BtnClickednone {
  background: #ccc;
}
.Addinput {
  padding: 10px;
  height: 30px;
  border-radius: 20px;
}
.AddBtn {
  padding: 10px;
  width: 80px;

  background: #ccc;
  border-radius: 20px;
  outline: none;
  border: none;
  font-family: main1;
  color: #fff;

  text-align: center;
  margin: 10px;
}
.AddBtn:hover {
  background-color: #ff5c00;
  cursor: pointer;
}
